import { KEYBOARD_LAYOUT } from '@/interfaces';

export const KeyboardLayout = () => {
  return <div className="flex flex-col gap-1 w-[650px] ml-5">
    {KEYBOARD_LAYOUT.map((row, rowIndex) => (
    <div key={rowIndex} className={`w-full justify-between flex gap-1${rowIndex === 0 ? ' mb-1' : ''}`}>
      {row.map((key, idx) => (
      <div
        key={idx}
        className={`text-xs flex flex-col items-center justify-center border border-gray-400 rounded bg-white font-mono text-black h-[40px] p-1 ${key.class ? key.class : 'w-[40px]'}`}
      >
        {key.shiftKey && <div className="text-xs text-gray-500 leading-none">{key.shiftKey}</div>}
        <div>{key.key}</div>
      </div>
      ))}
    </div>
    ))}
  </div>
}
